import React from 'react'
import {Field, reduxForm} from 'redux-form';
import { validateLogin as validate } from '../../Helper/Validate';
import Button from '../../Components/Button';

const LoginForm = ({handleSubmit, renderField, mySubmit, hasError, handleChangeClick}) => {
  return (
    <form onSubmit={handleSubmit(mySubmit)}>
      <h1>Sign In</h1>
      <Field name="email" label="Email" component={renderField} type="email"/>
      <Field name="password" label="Password" component={renderField} type="password" className="passwordInput" />
      <Button onClick={handleSubmit(mySubmit)} className="signInBtn">Sign In</Button>
      {
        hasError ?<span style={{marginTop: 10}}>{hasError}</span> : <div></div>
      }
      <div className="accountQuestion">
        <p>Not registered? <a onClick={handleChangeClick}>Sign Up</a></p>
      </div>
    </form>
  )
}

export default reduxForm({
    form: 'login',
    validate,
})(LoginForm)
