import React from 'react'

const Avatar = ({name, className, shadow, backColor, color}) => {
  return (
    <div className={`avatar ${className} ${shadow ? "shadow" : ""}`} style={{backgroundColor: backColor ? backColor : "#F5F6F8", color: color ? color : "rgb(73, 73, 73)"}}>
      {name.slice(0,1).toUpperCase()}
    </div>
  )
}

export default Avatar
