import React, { Component } from 'react'
import './Dashboard.css'
import AppBar from './AppBar';
import ChatList from './ChatList';
import Chat from './Chat';
import { connect } from 'react-redux';

import {getChats, setCurrentChat} from '../../Redux/actions/chats'
import {getUser} from '../../Redux/actions/user'

import io from 'socket.io-client';
import NewChat from './NewChat';

class Dashboard extends Component {

    constructor(props) {
        super(props);

        this.socket = io.connect();
    }

    state = {
        error: false,
        showNewChat: false
    }

    toggleNewChat = () => {
      this.setState({showNewChat: !this.state.showNewChat})
    }

    componentDidMount() {
        this.props.getUser().catch(err => this.setState({error: true}))
        this.props.getChats().then(chats => {
            if(chats.length > 0)this.props.setCurrentChat(chats[0]);
        }).catch(err => {this.setState({error: true})})
    }

  render() {
    const {error, showNewChat} = this.state;

    if(error) {
        return <h1>There was an Error!</h1>
    }

    return (
      <div className="dashboardContainer">
        <AppBar />
        <ChatList newChat={this.toggleNewChat}/>
        <Chat socket={this.socket} />
        {showNewChat ? <NewChat toggle={this.toggleNewChat} /> : <div></div>}
      </div>
    )
  }
}

const mapStateToProps = state => {
    return {
        chats: state.chats,
        user: state.user
    }
}

export default connect(mapStateToProps, {getChats, getUser, setCurrentChat})(Dashboard)