import React, { Component } from 'react'
import './ChatList.css'
import Avatar from '../../Components/Avatar';
import Divider from '../../Components/Divider';
import { TimelineLite } from 'gsap';
import {connect} from 'react-redux'

import {setCurrentChat} from '../../Redux/actions/chats';

class ChatList extends Component {

    state = {
        searchInput: "",
        isOpen: false
    }

    componentDidMount() {
        let tl = new TimelineLite({});
        let el = document.getElementsByClassName('chatListItemContainer');
        tl.staggerFrom(el, .5, {x: "-100%", opacity: 0}, .2);
    }

    generateMessage = (item) => {

        if(item.messages.length === 0){
            return "No messages until now"
        }

        let txt = item.messages[item.messages.length - 1].text

        if(txt.length <= 150) return txt;
        let shortM = txt.slice(0, 150)

        return shortM + "...";
    }

    generateDate= (item) => {

        if(item.messages.length === 0){
            return new Date().toLocaleDateString()
        }

        let message = item.messages[item.messages.length - 1].createdAt;
        return new Date(message).toLocaleDateString()
    }

    handleSearchChange = (e) => {
        this.setState({searchInput: e.target.value});
    }

    handleChatClick = (item) => () => {
        this.props.setCurrentChat(item)
    }

    handleListOpenClose = () => {
        this.setState({isOpen: !this.state.isOpen})
    }

    getName = (item) => {
        if (item.group) {
            return item.name
        }else {
            return item.members.find(el => el !== this.props.user.username)
        }
    }

  render() {
    const {searchInput, isOpen} = this.state;
    const {chats, currentChat, newChat} = this.props;
    const filteredChat = chats.filter(item => (this.getName(item).toLowerCase().includes(searchInput.toLowerCase())));
    return (
      <div className={`chatList ${isOpen ? "" : "listClose"}`}>
        <div className={`listContainer ${isOpen ? "" : "listConClose"}`}>
            <div className="chatListHeader">
                <h1>ChatApp</h1>
                <p onClick={newChat}>New Chat</p>
            </div>
            <div className="searchField">
                <svg fill="#8e8e8e" width="24" height="24">
                    <path d="M15.5 14h-.8l-.3-.3c1-1.1 1.6-2.6 1.6-4.2a6.5 6.5 0 1 0-2.3 5l.3.2v.8l5 5 1.5-1.5-5-5zm-6 0a4.5 4.5 0 1 1 0-9 4.5 4.5 0 0 1 0 9z"/>
                </svg>
                <input onChange={this.handleSearchChange} placeholder="Search" />
            </div>
            <Divider dir="hor" size="100%" />
            {
                filteredChat.map((item, i) => (
                    <div onClick={this.handleChatClick(item, i)} key={i} className={`chatListItemContainer ${item === currentChat ? "chatSelected" : ""}`}>
                        <div className="chatListItem">
                            <div>
                                <div className="avaName">
                                    <Avatar className="chatListAvatar" name={this.getName(item)} />
                                    <h2>{this.getName(item)}</h2>
                                </div>
                                <p>{this.generateDate(item)}</p>
                            </div>
                            <p>{this.generateMessage(item)}</p>
                        </div>
                        <Divider dir="hor" size="100%" />
                    </div>
                ))
            }
        </div>
        <div onClick={this.handleListOpenClose} className="openCloseChatList">
            <svg className={isOpen ? "svgOpen" : "svgClose"} width="24" viewBox="0 0 24 24">
                <path d="M8.6 16.6l4.6-4.6-4.6-4.6L10 6l6 6-6 6-1.4-1.4z"/>
                <path fill="none" d="M0 0h24v24H0V0z"/>
            </svg>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
    return {
        user: state.user,
        chats: state.chats,
        currentChat: state.currentChat
    }
}

export default connect(mapStateToProps, {setCurrentChat})(ChatList)

