import React, { Component } from 'react'
import './Register.css'
import TextField from '../../Components/TextField';
import SvgElement from './SvgElement';
import { TimelineLite } from 'gsap';

import {withRouter} from 'react-router-dom'

import {connect} from 'react-redux';
import {registerUser, loginUser} from '../../Redux/actions/user';
import LoginForm from './LoginForm';
import RegisterForm from './RegisterForm';

class Register extends Component {

  state = {
    signUp: false,
    registerError: undefined,
    loginError: undefined
  }
  
  handleChangeClick = () => {
    const tl = new TimelineLite({});
    let form = document.querySelectorAll('form > *');

    tl.staggerTo(form, .5, {scale: 0, opacity: 0}, .1, null, () => {
      this.setState({signUp: !this.state.signUp}, () => {
        form = document.querySelectorAll('form > *');
        tl.set(form, {scale: 0, opacity: 0})
        tl.staggerTo(form, .5, {scale: 1, opacity: 1}, .1)
      })
    })
  }

  handleRegisterClick = (e) => {
    this.props.registerUser({
      username: e.username,
      email: e.email,
      password: e.password1
    })
    .then(data => {
        this.props.history.push('/dashboard')
    })
    .catch(err => {
        this.setState({registerError: err})
    })
  }

  handleLoginClick = (e) => {
    this.props.loginUser(e)
      .then(data => {
          this.props.history.push('/dashboard')
      })
      .catch(err => {
          this.setState({loginError: err})
      })
  }

  render() {
    const {signUp, registerError, loginError} = this.state;
    return (
      <div className="registerContainer">
        <div className="registerCard">
          {
            signUp ? 
            <RegisterForm renderField={renderField} mySubmit={this.handleRegisterClick} hasError={registerError} handleChangeClick={this.handleChangeClick} />
            :
            <LoginForm renderField={renderField} mySubmit={this.handleLoginClick} hasError={loginError} handleChangeClick={this.handleChangeClick} />
          }
          <div>
            <SvgElement />
          </div>
        </div>
      </div>
    )
  }
}

const renderField = ({
  input,
  label,
  type,
  meta: { touched, error, warning },
  className
  }) => (

  <div>
      <TextField 
        {...input} 
        placeholder={label} 
        type={type} 
        error={touched && (error || warning)}
        className={className}
      />
      {touched &&
          ((error && <span>{error}</span>) ||
          (warning && <span>{warning}</span>))}
  </div>
)

export default connect(null, {registerUser, loginUser})(withRouter(Register));