import { GET_CHATS, SET_CURRENT_CHAT, UPDATE_CURRENT_CHAT } from "../../Constants";

export const chats = (state=[], action) => {
    switch (action.type) {
        case GET_CHATS:
            return action.payload
        case UPDATE_CURRENT_CHAT:
            return newMessage(state, action.payload)
        default:
            return state
    }
}

export const currentChat = (state={}, action) => {
    switch (action.type) {
        case SET_CURRENT_CHAT:
            return action.payload
        case UPDATE_CURRENT_CHAT: 
            return action.payload
        default:
            return state;
    }
}

const newMessage = (state, chat) => {
    console.log("NOW")
    let i = state.findIndex(x => x._id == chat._id);
    state[i] = chat;

    return [...state]
}