import { REGISTER_USER, LOGIN_USER, GET_USER, GET_ALL_USER } from "../../Constants";

export const user = (state={}, action) => {
    switch (action.type) {
        case REGISTER_USER:
            return action.payload
        case LOGIN_USER: 
            return action.payload
        case GET_USER: 
            return action.payload
        default:
            return state;
    }
}

export const allUsers = (state=[], action) => {
    
    if(action.type === GET_ALL_USER){
        return action.payload
    }

    return state;
}