import React from 'react'
import {Field, reduxForm} from 'redux-form';

import { validateRegister as validate, warn } from '../../Helper/Validate';
import Button from '../../Components/Button';

const RegisterForm = (props) => {
  const {handleSubmit, renderField, mySubmit, hasError, handleChangeClick} = props;
  return (
    <form onSubmit={handleSubmit(mySubmit)}>
      <h1>Sign Up</h1>
      <Field name="username" type="text" component={renderField} label="Username" />
      <Field name="email" type="email" component={renderField} label="Email" className="passwordInput" />
      <Field name="password1" type="password" component={renderField} label="Password" className="passwordInput" />
      <Field name="password2" type="password" component={renderField} label="Password" className="passwordInput" />
      <Button onClick={handleSubmit(mySubmit)} className="signInBtn">Sign Up</Button>
      {
        hasError ? <span style={{marginTop: 10}}>{hasError}</span> : <div></div>
      }
      <div className="accountQuestion">
        <p>Already have an Account? <a onClick={handleChangeClick}>Sign In</a></p>
      </div>
    </form>
  )
}

export default reduxForm({
    form: 'register',
    validate,
    warn
})(RegisterForm)
