import Axios from "axios";
import { URL, GET_CHATS, SET_CURRENT_CHAT, UPDATE_CURRENT_CHAT } from "../../Constants";
import {getToken} from "../../Helper/AxiosHelper";

export const getChats = () => (dispatch) => {
    return Axios.get(`${URL}/chats/`, {
        headers: {
            "x-auth": getToken() 
        }
    }).then(chats => {
        if(chats.status === 200){
            dispatch({type: GET_CHATS, payload: chats.data})
            return chats.data
        }else {
            Promise.reject();
        }
    }).catch(err => {
        Promise.reject(err);
    })
}

export const handleNewMessage = (chat) => {
    return {type: UPDATE_CURRENT_CHAT, payload: chat}
}

export const setCurrentChat = (chat) => {
    return {type: SET_CURRENT_CHAT, payload: chat}
}

export const createChat = (members, name) => (dispatch) => {

    let body = {};

    if(name) {
        body = {
            members,
            name
        }
    }else {
        body = {
            members
        }
    }

    console.log(body)

    return Axios.post(`${URL}/chats/new`, body, {
        headers: {
            'x-auth': getToken()
        }
    }).then(chat => {
        console.log(chat);
    })
}