import Axios from 'axios'
import { URL, REGISTER_USER, LOGIN_USER, JWT, GET_USER, GET_ALL_USER } from '../../Constants';
import { getToken } from '../../Helper/AxiosHelper';

export const registerUser = (data) => (dispatch) => {
    return new Promise((resolve, reject) => {
        Axios.post(`${URL}/users/register`, data)
        .then(user => {
            dispatch({type: REGISTER_USER, payload: user.data});
            if (user.status === 200) {
                localStorage.setItem(JWT, user.headers['x-auth'])
                resolve(user);
            }else {
                reject(user.status);
            }
        })
        .catch(err => {
            if(err.response.status === 400){
                reject("User already exist.");
            }else {
                reject("Something went wrong. Please try again later!")
            }
        })
    });
}

export const loginUser = (data) => (dispatch) => {
    return new Promise((resolve, reject) => {
        Axios.post(`${URL}/users/login`, data)
            .then(user => {
            
                if (user.status === 200) {
                    localStorage.setItem(JWT, user.headers['x-auth'])
                    resolve(user);
                    dispatch({type: LOGIN_USER, payload: user.data})
                }else {
                    reject(user.status);
                }
            })
            .catch(err => {
                if(err.response.status === 400){
                    reject("Wrong credentials.");
                }else {
                    reject("Something went wrong. Please try again later!")
                }
            })
    })
}

export const getUser = () => (dispatch) => {
    return Axios.get(`${URL}/users/me`, {
        headers: {
            'x-auth': getToken()
        }
    })
    .then(user => {
        if(user.status === 200){
            dispatch({type: GET_USER, payload: user.data})
            return user.data
        }else {
            Promise.reject();
        }
    })
    .catch(err => {
        Promise.reject(err);
    })

}

export const getAllUser = () => (dispatch) => {
    return Axios.get(`${URL}/users/all`, {
        headers: {
            'x-auth': getToken()
        }
    })
    .then(allUsers => {
        if(allUsers.status === 200){
            dispatch({type: GET_ALL_USER, payload: allUsers.data})
            return allUsers.data
        }else {
            Promise.reject();
        }
    }).catch(err => {
        Promise.reject(err);
    })
}