import React, { Component } from 'react'
import {connect} from 'react-redux';

import './NewChat.css'
import Divider from '../../Components/Divider';

import {getAllUser} from '../../Redux/actions/user';
import {createChat} from '../../Redux/actions/chats';

import TextField from '../../Components/TextField'

class NewChat extends Component {

    state = {
        searchInput: "",
        checkedUser: [],
        groupInput: "",
        showGroup: false,
        groupError: false
    }

    componentDidMount(){
        this.props.getAllUser()
    }

    handleSearch = (e) => {
        this.setState({searchInput: e.target.value})
    }

    handleOnChange = (user) => (e) => {
        let checked = e.target.checked;
        const {checkedUser} = this.state;

        if(checked){
            this.setState({
                checkedUser: [...checkedUser, user]
            })
        }else {
            let array = [...checkedUser];
            let index = array.indexOf(user);
            if (index > -1) {
                array.splice(index, 1);
            }
            this.setState({checkedUser: array});
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();

        const {checkedUser} = this.state;

        if(checkedUser.length > 1){
            this.toggleGroup()
        }else if(checkedUser.length === 1) {
            this.props.toggle()
            this.props.createChat(this.getUsernames(checkedUser));
        }

    }

    toggleGroup = () => {
        this.setState({showGroup: !this.state.showGroup, groupError: false})
    }

    handleGroupChange = (e) => {
        this.setState({groupInput: e.target.value})
    }

    handleGroupSubmit = () => {
        const {groupInput, checkedUser} = this.state;
        if(groupInput.length < 3){
            this.setState({groupError: true})
        }else {
            this.props.createChat(this.getUsernames(checkedUser), groupInput)
                .then(e => {
                    this.props.toggle();
                })
        }
    }

    getUsernames = (arr) => {
        return arr.map(item => item.username)
    }

  render() {
    const {allUsers, toggle, user} = this.props;
    const {searchInput, showGroup, groupError} = this.state;
    const users = allUsers.filter(item => item.username !== user.username)
    const allUsersFilter = users.filter(user => user.username.toLowerCase().includes(searchInput.toLowerCase()))
    return (
      <div className="newChatContainer">
        <div className="newChat">
            <div className="newChatHeader">
                <h1>New Chat</h1>
                <input 
                    placeholder="Search"
                    onChange={this.handleSearch}
                    value={searchInput}
                />
                <Divider dir="hor" size="100%" />
            </div>
            <div className="newChatUserContainer">
                {
                    allUsersFilter.map((user,i) => (
                        <div className="newChatUser" key={i}>
                            <div>
                                <h3>{user.username}</h3>
                                <p>{user.email}</p>
                            </div>
                            <input onChange={this.handleOnChange(user)} type="checkbox"/>
                        </div>
                    ))
                }
                <div className="newChatSpace"></div>
            </div>
            <div onClick={this.handleSubmit} className="newChatFab">
                <svg fill="#fff" width="24" height="24">
                    <path d="M3 17.3V21h3.8l11-11-3.7-3.8L3 17.2zM20.7 7c.4-.4.4-1 0-1.4l-2.3-2.3a1 1 0 0 0-1.4 0L15 5 19 9 20.7 7z"/>
                </svg>
            </div>
            <div onClick={toggle} className="newChatClickBack"></div>
            {showGroup ? 
            <form onSubmit={this.handleGroupSubmit} className="groupPopUp">
                <h2>Type in the group name</h2>
                <TextField error={groupError} onChange={this.handleGroupChange} placeholder="Name" type="text" />
                <button type="submit">Create</button>
                <div onClick={this.toggleGroup} className="newChatClickBack groupBack"></div>
            </form> :
            <div></div>
            }
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
    return {
        allUsers: state.allUsers,
        user: state.user
    }
}

export default connect(mapStateToProps, {getAllUser, createChat})(NewChat);