import React, { Component } from 'react'
import './Chat.css'
import Avatar from '../../Components/Avatar';
import Divider from '../../Components/Divider';
import Message from '../../Components/Message';
import { connect } from 'react-redux';

import {handleNewMessage, setCurrentChat} from '../../Redux/actions/chats'
import { TweenLite } from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';

let scrollHeight = undefined;

class Chat extends Component {

  state = {
    messageInput: ""
  }

  handleChangeInput = (e) => {
    this.setState({messageInput: e.target.value});
  }

  handleSendMessage = () => {
    const {socket, chat, user} = this.props;

    if(socket.connected) {
      socket.emit('createMessage', chat._id, user.username, this.state.messageInput);
      this.setState({messageInput: ""})
    }

  }

  componentDidMount() {
    this.props.socket.on('newMessage', (chat) => {
      this.setState({messageInput: ""})
      this.props.handleNewMessage(chat);
      this.props.setCurrentChat(chat)
    })
  }

  componentDidUpdate(prevProps) {
    let el = document.getElementsByClassName('messages')[0];

    if(this.props.chat !== prevProps.chat) {
      scrollHeight = undefined;

      const {socket, chat} = this.props;

      console.log(socket)
      socket.emit('joinRoom', chat._id)
    }
    
    if(el){
      if(el.scrollHeight !== scrollHeight){
        
        if(scrollHeight === undefined){
          scrollHeight = el.scrollHeight;
          return el.scrollTo(0, el.scrollHeight);
        }

        TweenLite.to(el, 2, {scrollTo: el.scrollHeight});
        scrollHeight = el.scrollHeight;
      }
    }

  }

  handleKeyPress = (target) => {
    if(target.charCode==13){
      this.handleSendMessage();
    } 
  }

  getName = (item) => {
    if (item.group) {
        return item.name
    }else {
        return item.members.find(el => el !== this.props.user.username)
    }
  }

  render() {
    const {chat, user} = this.props;
    const {username} = user;

    if (!chat.messages) {
      return <h1>No chats found...</h1>
    }

    return (
        <div className="chat">
            <div>
              <div className="chatHeader">
                <div>
                  <Avatar className="chatAvatar" color="white" backColor="#F85453" name={this.getName(chat)}/>
                  <h2>{this.getName(chat)}</h2>
                </div>
                <div className="moreBtnContainer">
                  <svg viewBox="0 0 24 24">
                    <path d="M12 8c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm0 2c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0 6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2z"/>
                  </svg>
                </div>
              </div>
              <Divider dir="hor" size="100%" />
            </div>
            <div className="messages">
              {
                chat.messages.map((item, i) => (
                  <Message key={i} message={item} username={username} />
                ))
              }
            </div>
            <div>
              <Divider dir="hor" size="100%" />
              <div className="messageInput">
                <div className="chatSvgContainer">
                  <svg fill="#404751" width="24" height="24">
                    <path d="M16.5 6v11.5a4 4 0 1 1-8 0V5a2.5 2.5 0 0 1 5 0v10.5c0 .6-.5 1-1 1s-1-.5-1-1V6H10v9.5a2.5 2.5 0 0 0 5 0V5a4 4 0 1 0-8 0v12.5a5.5 5.5 0 1 0 11 0V6h-1.5z"/>
                  </svg>
                </div>
                <input 
                  type="text" 
                  placeholder="Type your message" 
                  onChange={this.handleChangeInput}
                  value={this.state.messageInput}
                  onKeyPress={this.handleKeyPress}
                />
                <div className="chatSvgContainer">
                  <svg fill="#404751" width="24" height="24">
                    <path fill="none" d="M0 0h24v24H0V0z"/>
                    <path d="M12 2a7 7 0 0 0-7 7c0 5.3 7 13 7 13s7-7.8 7-13a7 7 0 0 0-7-7zm0 9.5a2.5 2.5 0 1 1 0-5 2.5 2.5 0 0 1 0 5z"/>
                  </svg>
                </div>
                <div onClick={this.handleSendMessage} className="chatSvgContainer">
                  <svg fill="#404751" width="24" height="24">
                    <path fill="none" d="M0 0h24v24H0V0z"/>
                    <path d="M2 21l21-9L2 3v7l15 2-15 2v7z"/>
                  </svg>
                </div>
              </div>
            </div>
        </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    user: state.user,
    chat: state.currentChat
  }
}

export default connect(mapStateToProps, {handleNewMessage, setCurrentChat})(Chat)